<template>
    <!-- Button trigger modal -->

    <!-- Modal -->
    <div
      class="modal fade mborder"
      id="deleteModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="deleteModalTitle"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="deleteModalTitle">
              {{ delete_modal_title }}
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            {{ delete_modal_body }}
          </div>
          <div class="modal-footer">
            <button v-on:click="$emit('freeze')" type="button" class="btn btn-secondary mborder">
              {{ freeze_btn_str }}
            </button>
            <button v-on:click="$emit('edit')" type="button" class="btn btn-secondary mborder">
              {{ edit_btn_str }}
            </button>
            <button v-on:click="$emit('delete')" type="button" class="btn btn-primary mborder">
              {{ delete_btn_str }}
            </button>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      delete_modal_title: "Xo k nim?",
      delete_modal_body:
        "Sau khi xo k nim, s khng c cch no  khi phc, bn c th dng tnh nng 'ng bng' hoc 'Chnh sa' nh mt la chn khc  khng b mt k nim ny!",
        delete_btn_str:"Xo",
        freeze_btn_str:"ng bng",
        edit_btn_str:"Chnh sa"
    };
  }
};
</script>

<style></style>



