<template>
  <div class="my-block">
    <ul class="list-group-sm no-padding-inline">
      <li
        class="list-group-item"
        v-for="(setting, i) in notificationSettings"
        v-bind:key="i"
      >
        <span>{{ setting.title }}</span>
        <SwitchElement
          class="float-right"
          v-bind:checked="setting.checked"
          v-on:updated="
            setting.checked = !setting.checked;
            log_info();
          "
        ></SwitchElement>
        <ul v-if="setting.checked" class="list-group-sm no-padding-inline">
          <li
            class="list-group-item"
            v-for="(option, i) in setting.options"
            v-bind:key="i"
          >
            <span>{{ option.title }}</span>
            <input
              v-if="option.type == 'frequency'"
              class="input-number"
              type="number"
              v-bind:value="option.values"
            />
            <span>{{ option.unit }}</span>
            <input
              class="float-right"
              type="checkbox"
              v-model="option.checked"
            />
          </li>
          <li class="list-group-item" v-for="(v, i) in setting.via" v-bind:key="i">
            <span>{{ v.method }}</span>
            <input class="float-right" type="checkbox" v-model="v.checked" />
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
import SwitchElement from "../../shared/SwitchElement";
export default {
  data() {
    return {
      remind_write_diary_str: "Nhc ti vit nht k",
      turn_on_notify_str: "Thng bo cc s kin",
      notificationSettings: [
        {
          title: "Nhc ti vit nht k",
          checked: false,
          options: [
            {
              op_id: "remind_when_no_active",
              checked: false,
              title: "Mi ngy",
              type: "time",
              values: 1
            },
            {
              op_id: "remind_when_no_active",
              type: "frequency",
              checked: false,
              unit: "ngy",
              title: "Sau khi khng c hot ng trong vng",
              values: 1
            }
          ],
          via: [
            {
              method: "Gi thng bo n Email",
              checked: false
            },
            {
              method: "Thng bo qua ng dng",
              checked: false
            }
          ]
        },
        {
          title: "Khi ng nhp trn thit b mi",
          checked: false,
          options: [],
          via: [
             {
              method: "Gi thng bo n Email",
              checked: false
            },
            {
              method: "Thng bo qua ng dng",
              checked: false
            }
          ]
        },
        {
          title: "Xem li cc k nim theo ch ",
          checked: false,
          options: [
            {
              title: "Gia nh",
              status: "checked"
            },
            {
              title: "Th thao",
              status: "checked"
            },
            {
              title: "Du lch",
              status: "checked"
            },
            {
              title: "m nhc",
              status: "checked"
            }
          ],
          via: [
            {
              method: "Gi thng bo n Email",
              checked: false
            },
            {
              method: "Thng bo qua ng dng",
              checked: false
            }
          ]
        },
        {
          title: "Gi cho ti cc mo s dng ng dng",
          checked: false,
          options: [],
          via: [
            {
              method: "Gi thng bo n Email",
              checked: false
            },
            {
              method: "Thng bo qua ng dng",
              checked: false
            }
          ]
        }
      ]
    };
  },
  components: {
    SwitchElement
  },
  methods: {
    log_info() {
      console.log("adfasdfads", this.notificationSettings);
    }
  }
};
</script>

<style>

.input-number {
  outline: none;
  width: 60px;
  border-radius: 32px;
  text-align: center;
}
</style>



